.main-nav {
    position: relative;
    width: auto;
    min-height: 51px;
    background-color: #826550;

    @media (min-width: @tablet-width) {
        background-color: #000;
        width: 640px;
        margin: 0 auto;
    }
    
    @media (min-width: @desktop-width) {
        width: 960px;
    }
}

.main-nav__toggle {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 2;
    display: block;
    width: 70px;
    height: 50px;
    background-color: #6A4D38;
    cursor: pointer;
    border: none;

    &:hover,
    &:active {
        background-color: #624530;
    }

    @media (min-width: @tablet-width) {
            display: none;
    }
}

.main-nav--closed .main-nav__toggle::before {
    content: "";
    position: absolute;
    top: 15px;
    left: 17px;
    width: 36px;
    height: 2px;
    background-color: #fff;
    box-shadow: 
        0 8px 0 0 #fff,
        0 16px 0 0 #fff;
}

.main-nav--closed .main-nav__toggle:active:before {
    background-color: fade(#fff, 30%);
    box-shadow:
        0 8px 0 0 fade(#fff, 30%),
        0 16px 0 0 fade(#fff, 30%);
}

@media(max-width: @mobile-width-only) {
    .main-nav--closed .site-list__item {
        display: none;

        &--active {
            display: block;
        }
    }

    .main-nav--closed .site-list__item--active a {
        padding-right: 70px;
        padding-left: 70px;
        text-align: center;
    }

    .main-nav--closed .user-list__item {
        display: none;
    }

    .main-nav--opened .main-nav__wrapper {
        position: absolute;
        top: 0;
        right: 0;
        left: 70px;
        z-index: 10;
        min-height: 54vh;
        background-color: #6a4d38;
    }

    .main-nav--opened .main-nav__wrapper::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        z-index: 2;
        width: 7px;
        height: 100%;
        box-shadow: inset 4px 0 7px -2px fade(#000, 40%);
    }

    .main-nav--nojs .main-nav__toggle {
        display: none;
    }

    .main-nav--nojs .main-nav__wrapper {
        position: static;
        min-height: 0;
    }

    .main-nav--nojs .site-list__item {
        display: block;
    }

    .main-nav--nojs .site-list__item--active a {
        padding-right: 21px;
        padding-left: 21px;
        text-align: left;
    }

    .main-nav--nojs .user-list__item {
        display: block;
        background-color: #6a4d38;
    }
}

.main-nav--opened .main-nav__toggle {
    top: 0;
    right: auto;
    left: 0;

    &::before,
    &::after {
        content: "";
        position: absolute;
        top: 24px;
        left: 18px;
        width: 36px;
        height: 2px;
        background-color: #fff;
    }

    &::before {
        transform: rotate(45deg);
        box-shadow: none;
    }

    &::after {
        transform: rotate(-45deg);
    }

    &:active::before,
    &:active::after {
        background-color: fade(#fff, 30%);
    }
}

.main-nav__wrapper {

    @media (min-width: @tablet-width) {
        display: flex;
        justify-content: space-between;
        width: 640px;
        margin: 0 auto;
    }

    @media (min-width: @desktop-width) {
        width: 960px;
    }
}
